import axios from 'axios';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';

function Subtela() {  
      const navigate = useNavigate();

    const [mostrarTermos, setMostrarTermos] = useState(true);
    const [aceitouTermos, setAceitouTermos] = useState(false);
    const aceitar=()=>{
        toast.error('ACEITE OS TERMOS DE USO PARA ULTILIZAR O CONECTA', {
            position: "top-center",
            autoClose: 2500,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
        })
    }
    const aceitarTermos = () => {
        
        const savedUserData = localStorage.getItem("@detailUser");
      if (savedUserData) {
          const userData = JSON.parse(savedUserData);
          var id = userData.uid;
          var cod = userData.email;
      } else {
          console.log("Nenhuma informao encontrada no localStorage.");
      }
      axios.get(`https://othondecarvalho.com.br:5555/pc/updateTermos/${cod}`, {
        headers: { 'Authorization': `Bearer ${id}` },
      }).then(Responses => {
        if (Responses.data === 'Aceito') {
            
        setAceitouTermos(true);
        setMostrarTermos(false); 
          navigate(`/Principal`);
          window.location.reload(true);
           
        } else {
            setAceitouTermos(false);
        setMostrarTermos(true);  
        }
      }
      ).catch(
        Responses => {
          localStorage.removeItem("@detailUser");
        }
      )
    };
    // navigate(`/Principal`)
    // window.location.reload(true);

    return (
        <div className='tamanho' style={{ textAlign: 'center' }}>
            <ToastContainer />
            {aceitar()}
            {mostrarTermos && (
                <div
                    style={{
                        position: 'fixed',
                        top: '50%',
                        left: '50%',
                        transform: 'translate(-50%, -50%)',
                        backgroundColor: 'white',
                        padding: '25px',
                        boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.5)',
                    }}
                >
                    <h2>Termos de Uso Conecta</h2>
                    <div style={{
                        overflowY: 'scroll',
                        maxHeight: '600px',
                        textAlign: 'left'
                    }}>
                        <p><b>Contrato de Parceria entre profissionais (Arquiteto - Decorador  Designer - Lighting Designer)
                            junto  Othon de Carvalho</b><br /></p>

                        <p><b>Clusula 1. Objeto do contrato</b></p>

                        <p>
                            O presente contrato tem como objeto a parceria comercial entre as Partes, para promover o fomento, assessoria e indicao de vendas de produtos comercializados pela <b>Othon de Carvalho (Othon).</b>
                        </p>

                        <p>Pargrafo 1- O Profissional dever fazer cadastro na Othon via link, via site ou presencialmente na loja antes de iniciar a parceria, esclarecendo eventuais dvidas pelo telefone: (31) 982252486.</p>

                        <p><b>Clusula 2. Descrio da parceria.</b></p>

                        <p>2.1. Ao cumprir o objeto deste contrato o Profissional receber o percentual de 10% (dez por cento) sobre as vendas de <b>produtos de iluminao</b>(mercadorias de iluminao decorativa,
                            tais como pendente, arandela, plafon, lustres, spots, perfil e seus acessrios, abajur, coluna, luminria de mesa, fita de led, trilho e projetores especiais).</p>

                        <p>2.2. Em relao a participao do profissional nas vendas de acessrios e materiais eltricos (inserindo, nessa modalidade,
                            produtos como drives e lmpadas), ser devido ao Profissional o percentual de 2% (dois por cento) sobre as vendas efetivamente realizadas.</p>

                        <p>2.3. O percentual previsto nos itens 2.1 e 2.2 a ser repassado ao Profissional ser calculado com base no valor lquido das vendas
                            efetuadas devido  sua interveno, assessoria e indicao presenciais, ressaltando-se que, caso seja emitida nota fiscal pelo parceiro,
                            o valor dos impostos incidentes na operao ser descontado do percentual a ser repassado pela Othon.</p>

                        <p>2.3.1. Em nenhuma hiptese ser concedido ao Profissional percentual sobre
                            eventuais vendas que o mesmo no tenha participado da negociao presencialmente
                            ou com comunicado formal por escrito (com o vendedor ou equipe Luminato) antes do oramento. .</p>

                        <p>2.3.2. Todas as vendas sero consideradas efetivadas quando o consumidor final efetuar o pagamento devido  Othon, conforme descrito das respectivas notas fiscais.</p>

                        <p>Pargrafo 1. O Profissional se compromete a solicitar ao vendedor sua vinculao no oramento, sob pena de no recebimento do percentual
                            sobre a venda na qual efetivamente participou (assessorou/participou), sendo este procedimento de sua inteira responsabilidade.</p>

                        <p>Pargrafo 2. Os dados do Profissional (CPF ou CNPJ) devem ser inseridos no pedido/oramento, sob pena de no receber qualquer percentual sobre a venda dos produtos em que tenha efetivamente participado.</p>

                        <p>2.4. Ao celebrar este contrato, o Profissional se compromete a cadastrar e participar do programa de pontuao da Othon, denominado Conecta,
                            por meio do qual a cada R$1,00 (um real) em venda que efetivamente participar, o Profissional ganhar 1 (um) ponto, exceto nos casos de vendas de condutores eltricos.</p>

                        <p>2.5. A participao do Profissional no programa Conecta poder, a critrio da Othon, propiciar ao Profissional diversos benefcios, tais como prmios bimestrais,
                            semestrais e anuais, a serem previa e periodicamente divulgados pela Othon, atravs de canais prprios, a depender da campanha publicitria vigente  poca,
                            devendo ser observadas pelo Profissional habilitado as regras de pulverizao, ranking e pontuao mnima para fazer jus aos benefcios.</p>

                        <p><b>Clusula 3. Pagamentos.</b></p>

                        <p>A Othon realizar o pagamento dos percentuais sobre as vendas das mercadorias ao Profissional somente aps a emisso da Nota Fiscal de remessa das mercadorias ao consumidor final,
                            por meio de depsito bancrio, ou no carto presente.</p>

                        <p>Pargrafo 1. A Othon, em estrito cumprimento  legislao, s realizar pagamentos aos Parceiros, da seguinte forma e prazos:</p>

                        <p>I - Pessoa Jurdica: Emisso de Nota Fiscal de Servios pelo Parceiro ao setor responsvel da Othon, com deduo de impostos devidos para o tipo de servio prestado;
                            II - Pessoa Fsica: Depsito em carto presente.
                            <b>III -  O pagamento acontecer no ms subsequente  venda efetivada, com apurao dos valores at o 5 dia til com pagamentos nos dias 15 e 30 de cada ms.</b></p>

                        <p><b>Clusula 4. Prazos</b></p>

                        <p>Aps a data da emisso da Nota Fiscal de remessa de mercadorias ao consumidor final, a Othon conceder um prazo mximo de 90 (noventa)
                            dias corridos para que o Profissional requeira e apresente os documentos para recebimento da contraprestao, observados os pargrafos 1 e 2 da Clusula 3.</p>

                        <p><b>Pargrafo 1.</b> Uma vez expirado o prazo de 90 (noventa) dias corridos, sem que o Profissional apresente a Nota Fiscal de Servios ou solicite o pagamento via carto presente,
                            o saldo ser automaticamente baixado no sistema, sem possibilidade de resgate posterior.</p>

                        <p><b>Clusula 5. Disposies gerais.</b></p>

                        <p>Nenhuma retribuio ser devida ao Profissional se a falta de pagamento resultar de insolvncia do comprador/consumidor,
                            bem como, se o negcio vier a ser desfeito pelo consumidor, ou for sustada a entrega da mercadoria por ser duvidosa a liquidao.</p>

                        <p>Pargrafo 1. A Othon se reserva o direito de auditar todas as vendas para certificar que o Profissional de fato prestou servio de interveno, assessoria e indicao presencial de vendas.</p>

                        <p>Pargrafo 2. Verificada qualquer irregularidade na indicao do Profissional, bem como declarao do Cliente final,
                            verbal ou escrita, de que desconhece o Profissional ou que o mesmo no realizou o servio objeto do presente termo, a comisso ser automaticamente excluda.
                        </p>
                        <p><b>Clusula 6.</b> O presente contrato no cria entre as partes qualquer espcie de vnculo empregatcio, sociedade, agncia, associao ou consrcio.</p>

                        <p><b>Clusula 7.</b> A Othon se resguarda no direito de alterar os termos deste contrato, sobretudo para criar novas regras ao programa de prmios e pontuao,
                         sendo que as alteraes tero validade apenas aps a comunicao ao Profissional.</p>

                        <p><b>Clusula 8.</b> O presente contrato  firmado por prazo indeterminado, podendo ser rescindido unilateralmente e imotivadamente por qualquer uma das partes,
                         sem prvio e qualquer necessidade de pagamento de multa contratual.</p>

                        <p><b>Clusula 9.</b> As Partes devero manter a confidencialidade dos termos e condies deste Contrato, observado que qualquer divulgao relativa a
                         este Contrato to-somente poder ocorrer para cumprimento de legislao aplicvel ou exigncia de rgo regulador.</p>
                        <p><b>Clusula 10.</b> As <b>PARTES</b> declaram neste ato que esto cientes, conhecem e entendem os termos das normas anticorrupo brasileiras,
                         especialmente a lei 12.846/2013, alm de quaisquer outras aplicveis sobre o objeto do presente Contrato ("Regras Anticorrupo"),
                          comprometendo-se a abster-se de qualquer atividade que constitua uma violao das disposies destas.</p>
                        <p>Pargrafo nico - Para os fins da presente clusula, as <b>PARTES</b> declaram, ainda,
                         que: (a) no violaram ou violaro as Regras Anticorrupo; (b) tem cincia que qualquer atividade que viole as Regras Anticorrupo  proibida e que conhecem as consequncias possveis de tal violao.</p>
                        <p><b>Clusula 11.</b> As <b>PARTES</b>, em comum acordo, estabelecem que cumpriro os deveres e as obrigaes referentes  proteo e ao tratamento de dados pessoais 
                        relacionados com o presente Contrato (Dados Pessoais) de acordo com a legislao vigente aplicvel, incluindo, mas no se limitando   n 12.965/2014 
                        e Decreto Federal n 8.771/2016 (Marco Civil da Internet), Lei Federal n 13.709/2018 (Lei Geral de Proteo de Dados ou LGPD)
                         e a regulamentao porventura editada pela Autoridade Nacional de Proteo de Dados (ANPD), bem como respeitaro as polticas de proteo de
                          dados pessoais e segurana da informao estabelecidas por cada PARTE (legislao, regulamentao e polticas em conjunto denominadas Normas de Proteo de Dados Pessoais).</p>
                        <p><b><u>Pargrafo primeiro. O Parceiro declara de forma expressa a sua cincia e o seu consentimento quanto ao processamento, tratamento e armazenamento,
                         pela Othon de Carvalho, dos seus dados, inclusive os dados sensveis, que sejam necessrios para o fiel cumprimento deste contrato, autorizando expressamente que tais dados,
                          inclusive os dados sensveis, podero ser compartilhados com terceiros, sobretudo para propiciar ao Profissional os benefcios oriundos do programa conecta.</u></b></p>
                        <p><b><u>Pargrafo segundo. O Parceiro consente expressamente com a divulgao de seus dados pessoais nas redes sociais da Othon.</u></b></p>
                        <p><b><u>Pargrafo terceiro. O consentimento ora expresso engloba todos os dados fornecidos  Othon pelo Parceiro ou que venham a ser obtidos por esta, 
                            seja de qual natureza for, inclusive os dados sensveis. O Parceiro concorda expressamente que os seus dados podero ser processados, tratados e armazenados mesmo aps o encerramento da parceria.</u></b></p>
                        <p><b>Clausula 12</b>. O Parceiro concede  Othon o direito no exclusivo e irrevogvel de usar, reproduzir,
                         distribuir, exibir, publicar, modificar e adaptar sua imagem nas Redes Sociais da Othon, sem qualquer restrio quanto ao tempo ou territrio,
                          em conformidade com as polticas e diretrizes das Redes Sociais.</p>
                        <p><b>Pargrafo primeiro.</b> A autorizao abrange o uso da imagem em qualquer tipo de contedo produzido pela Empresa, incluindo, mas no se limitando a, posts, stories, vdeos, anncios e material promocional.</p>
                        <p><b>Pargrafo segundo.</b> O Titular da Imagem declara que a autorizao concedida  gratuita, ou seja, no implica o pagamento de qualquer quantia pelo uso de sua imagem nas Redes Sociais da Empresa.</p>
                        <p><b>Clusula 13.</b> As <b>PARTES</b> elegem o foro da Comarca da Cidade de Belo Horizonte/MG, com renncia expressa a qualquer outro por mais privilegiado que seja, 
                        para dirimir dvidas decorrentes do presente Contrato e de sua execuo.</p>
                        <p>E, por estarem assim justas e acordadas, as <b>PARTES</b> assinam o presente instrumento em 2 (duas) vias de igual teor e para um s efeito, na presena das testemunhas abaixo.</p>
<br/><br/>

                        <p><b>OTHON DE CARVALHO E CIA LTDA</b><br/>
                        (CNPJ n: 17.185.679/0001-33)</p>





                    </div>
                    <button onClick={aceitarTermos}>Concordo com os Termos de Uso e li a Poltica de Privacidade</button>
                </div>
            )}

            {/* {aceitouTermos && navigate(`/Principal`)} */}
        </div>
    );
}

export default Subtela;
